.skill-circle:hover {
    transform: scale(1.1); /* Hover effect to slightly enlarge */
}

.skill-circle span {
    text-align: center;
    max-width: 70%; /* Adjust for text wrapping */
    padding: 0 5px; /* Padding to keep text centered */
    font-weight: 600;
}

/* Layout Adjustments for Rows */
@media (min-width: 768px) {
    .skills-grid {
        grid-template-columns: repeat(4, 1fr); /* 4 items per row */
    }
}

@media (max-width: 767px) {
    .skills-grid {
        grid-template-columns: repeat(2, 1fr); /* 2 items per row on tablets */
    }
}

@media (max-width: 480px) {
    .skill-circle {
        width: 80px; /* Smaller circles on mobile */
        height: 80px;
        font-size: 12px;
    }
}
